var defaultRecipes={"09534ef4-8ddf-4a06-b7da-a9be9997ccf6":{"title":"Cauliflower Lazagne","ingredients":"1 large cauliflower, trimmed, leaves removed; 8 fresh lasagne sheets; 2 x 500g jars Heinz Four Cheese Lasagne Bake Sauce; 3 cups grated tasty cheese; 1/4 teaspoon ground nutmeg; extra virgin olive oil, for brushing; c fresh flat-leaf parsley, to serve; mixed salad leaves, to serve.","description":"1. Preheat oven to 200C/180C fan-forced. Grease a 2.5-litre (10-cup-capacity) baking dish.\n2. Cut two 5mm-thick steaks from centre of cauliflower. Cut remaining cauliflower into small florets. Cook cauliflower steaks in a large saucepan of boiling water for 4 minutes or until almost tender. Using tongs, transfer to a plate lined with paper towel. Cook cauliflower florets in same pan of boiling water for 3 minutes or until almost tender. Drain well.\n3. Place 2 lasagne sheets into base of prepared dish, trimming to fit. Top with 1/2 cup of the sauce, spreading to cover. Sprinkle with 1/4 of the cauliflower florets. Sprinkle with 1/2 cup cheese and a pinch of nutmeg. Season with salt and pepper. Repeat process 3 more times. Top with remaining lasagne sheets. Spread with remaining sauce to cover. Sprinkle with remaining cheese and remaining nutmeg. Top with cauliflower steaks. Brush cauliflower steaks with oil.\n4. Bake for 40 minutes or until lasagne is golden. Stand for 10 minutes. Sprinkle with parsley. Serve with salad.","image":"data:image/jpeg;base64,","id":"09534ef4-8ddf-4a06-b7da-a9be9997ccf6"},"e5b72a6d-2e68-4eac-b110-5e9cb06676c4":{"title":"Spaghetti alla Carbonara","ingredients":"1 pound dry spaghetti; 2 tablespoons extra-virgin olive oil;\n4 ounces pancetta or slab bacon, cubed or sliced into small strips; 4 garlic cloves, finely chopped; 2 large eggs;\n1 cup freshly grated Parmigiano-Reggiano, plus more for serving; Freshly ground black pepper.","description":"1. Prepare the sauce while the pasta is cooking to ensure that the spaghetti will be hot and ready when the sauce is finished; it is very important that the pasta is hot when adding the egg mixture, so that the heat of the pasta cooks the raw eggs in the sauce.\n2. Bring a large pot of salted water to a boil, add the pasta and cook for 8 to 10 minutes or until tender yet firm (as they say in Italian \"al dente.\") Drain the pasta well, reserving 1/2 cup of the starchy cooking water to use in the sauce if you wish.\n3. Meanwhile, heat the olive oil in a deep skillet over medium flame. Add the pancetta and saute for about 3 minutes, until the bacon is crisp and the fat is rendered. Toss the garlic into the fat and saute for less than 1 minute to soften.\n4. Add the hot, drained spaghetti to the pan and toss for 2 minutes to coat the strands in the bacon fat. Beat the eggs and Parmesan together in a mixing bowl, stirring well to prevent lumps. Remove the pan from the heat and pour the egg/cheese mixture into the pasta, whisking quickly until the eggs thicken, but do not scramble (this is done off the heat to ensure this does not happen.) Thin out the sauce with a bit of the reserved pasta water, until it reaches desired consistency. Season the carbonara with several turns of freshly ground black pepper and taste for salt. Mound the spaghetti carbonara into warm serving bowls and garnish with chopped parsley. Pass more cheese around the table.","image":"data:image/jpeg;base64,","id":"e5b72a6d-2e68-4eac-b110-5e9cb06676c4"},"02c81b69-8104-4b30-b39c-068aecb63180":{"title":"Tiramissu","ingredients":"3 eggs; 1/2 cup caster sugar; 1/2 tsp vanilla extract; 8 oz / 250g mascarpone; 1 1/4 cups hot espresso coffee - strong!; 2 tbsp (or more!) of liquor of choice; 6.5 oz / 200g (24 - 30) lady fingers; cocoa.","description":"1. Beat yolks and sugar in stand mixer on medium high for 12 minutes or until white and thick. \n2. Add vanilla and mascarpone, beat until just combined. Transfer mixture to a bowl, set aside.\n3. Clean bowl and whisk. Beat egg whites until stiff.\n4. Fold 1/3 of the yolk mixture into the egg whites. Then gradually fold the remaining yolk mixture in and mix until just combined. \n5. Mix coffee and liquor together. Quickly dip biscuits in and line the bottom of a 8/20cm square dish.  \n6. Spread over half the cream, then top with another layer of coffee dipped biscuits.\n7. Spread with remaining cream.\n8. Cover, refrigerate for at least 3 hours, preferably overnight\n9. Dust with cocoa powder just before serving - either before you cut or after placing onto serving plates. \n","image":"data:image/jpeg;base64,","id":"02c81b69-8104-4b30-b39c-068aecb63180"}};/* harmony default export */ __webpack_exports__["a"] = (defaultRecipes);

